import { gql } from '@apollo/client';

export const LOGIN = gql`
  mutation login($email: String!, $password: String!) {
    login(email: $email, password: $password) {
      token
      user {
        _id
      }
    }
  }
`;

export const ADD_ORDER = gql`
  mutation addOrder($products: [ID]!) {
    addOrder(products: $products) {
      purchaseDate
      products {
        _id
        name
        description
        price
        quantity
        category {
          name
        }
      }
    }
  }
`;

export const ADD_USER = gql`
  mutation addUser(
    $firstName: String!
    $lastName: String!
    $email: String!
    $password: String!
  ) {
    addUser(
      firstName: $firstName
      lastName: $lastName
      email: $email
      password: $password
    ) {
      token
      user {
        _id
      }
    }
  }
`;

export const ADD_COMMENT = gql`
  mutation addComment($productId: ID!, $commentText: String!){
    addComment(productId: $productId, commentText: $commentText){
      _id
      name
      description
      price
      quantity
      category {
        name
      }
      comments{
        commentAuthor
        commentText
        commentDate
      }
    }
  }
`;

export const ADD_PRODUCT = gql`
mutation addProduct(
  $name: String!
  $description: String!
  $images: [String!]
  $price: Integer
  $quantity: Integer
  
) {
  addProduct(
    name: $name
    description: $description
    images: $images
    price: $price
    quantity: $quantity
  ) 
  }
`;